import React from 'react'
import propTypes from 'prop-types'
import { connect } from 'react-redux'

import Tag from './Tag'

class TagList extends React.Component{

	constructor(props){
		super(props)
	}



	render(){
		return(
			<ul id="tags">
				<li>{this.state.tags}</li>
			</ul>
		)
	}





}

TagList.propTypes = {
	tags : propTypes.array,
}



export default connect(
	state => ({
		tags: state.tags
	}), {

	}
)(TaskList)



